<template>
  <div class="box">
    <br>
    <el-form :inline="true">
      <el-form-item label="打一块钱本">
        <el-radio-group v-model="ben_0">
          <el-radio-button :label="30">30</el-radio-button>
          <el-radio-button :label="40">40</el-radio-button>
          <el-radio-button :label="50">50</el-radio-button>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="计算器">
        <el-input-number
          controls-position="right"
          v-model="num_1"
          :min="0"
        ></el-input-number>
      </el-form-item>

      <el-form-item label="X">
        <el-input-number
          controls-position="right"
          v-model="num_2"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="结果：">
        {{ num_1 * num_2 }}
      </el-form-item>
    </el-form>

    <br>

    <el-form :inline="true">
      <el-form-item label="基数(首项) 1">
        <el-input-number
          controls-position="right"
          v-model="a"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="公比 1">
        <el-input-number
          controls-position="right"
          v-model="q"
          :step="0.1"
          :min="0"
        ></el-input-number> </el-form-item
      ><el-form-item label="第几期(中奖) 1">
        <el-input-number
          controls-position="right"
          v-model="n"
          :min="1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="系列期数">
          <el-input
            controls-position="right"
            v-model="nStr"
          ></el-input>
        </el-form-item>
    </el-form>
    应打：{{ (Math.pow(q, n - 1) * a).toFixed(2) }} | 本钱：{{
      ben.toFixed(2)
    }}
    | 中奖：{{ zhon.toFixed(2) }} | 赢钱：{{ (zhon - ben).toFixed(2) }} |
    日均：{{ ((zhon - ben) / n).toFixed(2) }}
    | 系列总数：{{ total.toFixed(2) }}
    <br /><br /> <br>
    <el-form :inline="true">
      <el-form-item label="基数(首项) 2">
        <el-input-number
          controls-position="right"
          v-model="a_2"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="公比 2">
        <el-input-number
          controls-position="right"
          v-model="q_2"
          :step="0.05"
          :min="0"
        ></el-input-number> </el-form-item
      ><el-form-item label="第几期(中奖) 2">
        <el-input-number
          controls-position="right"
          v-model="n_2"
          :min="1"
        ></el-input-number>
      </el-form-item>
    </el-form>
    <!-- {{ typeof 1.4567.toFixed(2) }} string -->
    应打：{{ (Math.pow(q_2, n_2 - 1) * a_2).toFixed(2) }} | 本钱：{{
      ben_2.toFixed(2)
    }}
    | 中奖：{{ zhon_2.toFixed(2) }} | 赢钱：{{ (zhon_2 - ben_2).toFixed(2) }} |
    日均：{{ ((zhon_2 - ben_2) / (n + n_2)).toFixed(2) }}
    <!-- | 系列总数：{{ total_2.toFixed(2) }} -->

    <br /><br /> <br>
    <el-form :inline="true">
      <el-form-item label="基数(首项) 3">
        <el-input-number
          controls-position="right"
          v-model="a_3"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="公比 3">
        <el-input-number
          controls-position="right"
          v-model="q_3"
          :step="0.01"
          :min="0"
        ></el-input-number> </el-form-item
      ><el-form-item label="第几期(中奖) 3">
        <el-input-number
          controls-position="right"
          v-model="n_3"
          :min="1"
        ></el-input-number>
      </el-form-item>
    </el-form>
    <!-- {{ typeof 1.4567.toFixed(2) }} string -->
    应打：{{ (Math.pow(q_3, n_3 - 1) * a_3).toFixed(2) }} | 本钱：{{
      ben_3.toFixed(2)
    }}
    | 中奖：{{ zhon_3.toFixed(2) }} | 赢钱：{{ (zhon_3 - ben_3).toFixed(2) }} |
    日均：{{ ((zhon_3 - ben_3) / (n + n_2 + n_3)).toFixed(2) }}

    <br /><br />
  </div>
</template>

<script>
  export default {
    name: "Win",

    data() {
      return {
        ben_0: 30,

        a: 1,
        q: 2,
        n: 10,

        a_2: 1,
        q_2: 1.5,
        n_2: 1,

        a_3: 1,
        q_3: 1.3,
        n_3: 1,

        num_1: 0,
        num_2: 0,

        nStr: "",

        //排列五万位nStr 5 2 1 1 1 1 4 1 1 1 1 1 2 2 1 3 1 4 2 1 1 1 4 4 3 4 1 2 3 4 1 2 2 1 2 1 2 8 3 3 1 5 2 5 2 1 1 1 3 2 3 4 4 8 1 1 1 1 7 1 2
      };
    },

    computed: {
      // 本
      ben() {
        return (
          ((Math.pow(this.q, this.n) - 1) / (this.q - 1)) * this.ben_0 * this.a
        );
      },
      // 中
      zhon() {
        return Math.pow(this.q, this.n - 1) * 95 * this.a;
      },
      total() {
        let sum = 0;
        let n = this.nStr.split(" ");
        console.log(n);
        for (let i = 0; i < n.length; i++) {
          let num = parseInt(n[i]);
          if (!num) continue;
          sum =
            sum +
            (Math.pow(this.q, num - 1) * 95 * this.a -
              ((Math.pow(this.q, num) - 1) / (this.q - 1)) *
                this.ben_0 *
                this.a);
        }
        return sum;
      },

      // 本2
      ben_2() {
        return (
          ((Math.pow(this.q_2, this.n_2) - 1) / (this.q_2 - 1)) *
            this.ben_0 *
            this.a_2 +
          this.ben
        );
      },
      // 中2
      zhon_2() {
        return Math.pow(this.q_2, this.n_2 - 1) * 95 * this.a_2;
      },

      // 本3
      ben_3() {
        return (
          ((Math.pow(this.q_3, this.n_3) - 1) / (this.q_3 - 1)) *
            this.ben_0 *
            this.a_3 +
          this.ben_2
        );
      },
      // 中3
      zhon_3() {
        return Math.pow(this.q_3, this.n_3 - 1) * 95 * this.a_3;
      },
    },
  };
</script>

<style lang="css" scoped>
  .box {
    text-align: center;
  }
</style>
