<template>
  <div class="filter">
    <div class="head">
      <el-form :inline="true" class="form">
        <el-form-item>
          <el-radio-group v-model="flag">
            <el-radio-button :label="0">8 X X 8 (头尾)</el-radio-button>
            <el-radio-button :label="1">8 X 8 X</el-radio-button>
            <el-radio-button :label="2">8 8 X X</el-radio-button>
            <el-radio-button :label="3">X 8 8 X (中肚)</el-radio-button>
            <el-radio-button :label="4">X X 8 8</el-radio-button>
            <el-radio-button :label="5">X 8 X 8</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-input-number
            controls-position="right"
            v-model="money"
            ref="ipt1"
            :min="0"
            @focus="$refs.ipt1.select()"
          ></el-input-number>
        </el-form-item>
      </el-form>
    </div>

    <div class="show">
      <el-row v-if="flag === 0" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= money ? 0 : 1 }">
            <p>{{ i - 1 }}XX{{ index }}</p>
            <span>{{ value - money }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 1" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= money ? 0 : 1 }">
            <p>{{ i - 1 }}X{{ index }}X</p>
            <span>{{ value - money }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 2" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= money ? 0 : 1 }">
            <p>{{ i - 1 }}{{ index }}XX</p>
            <span>{{ value - money }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 3" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= money ? 0 : 1 }">
            <p>X{{ i - 1 }}{{ index }}X</p>
            <span>{{ value - money }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 4" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= money ? 0 : 1 }">
            <p>XX{{ i - 1 }}{{ index }}</p>
            <span>{{ value - money }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 5" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= money ? 0 : 1 }">
            <p>X{{ i - 1 }}X{{ index }}</p>
            <span>{{ value - money }}</span>
          </div>
        </el-col>
      </el-row>

      <div class="total">
        此页数目：{{ total[flag] }} &nbsp;&nbsp;全部数目：{{
          total[0] + total[1] + total[2] + total[3] + total[4] + total[5]
        }}
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "HistoryFilter",
    components: {},
    data() {
      return {
        flag: 0,
        money: 10,
      };
    },
    props: ["datas"],

    computed: {
      //头尾总数
      total() {
        const arr = this.datas;
        const total = [0, 0, 0, 0, 0, 0];

        arr.forEach((arr2, index) => {
          arr2.forEach((arr3) => {
            arr3.forEach((value) => {
              if (value > this.money) total[index] += value - this.money;
            });
          });
        });
        return total;
      },
    },
  };
</script>

<style lang="css" scoped>
  .filter {
    width: 100%;
  }
  .filter .head {
    padding: 7px;
  }

  .filter .form .el-form-item {
    margin: 0 5px;
  }

  .activated {
    background-color: #bfc;
  }

  .show .el-row .el-col {
    border: 1.5px solid #e4e1e1;
    padding: 5px;
    width: 10%;
  }

  .show .el-row:hover,
  .show .total:hover {
    background-color: #bfc;
    /* border: 0; */
  }

  .show .el-row .el-col {
    border-right: 0;
    border-bottom: 0;
  }

  .show .el-col:first-child {
    border-left: 0;
  }

  .show .el-row .el-col span {
    color: rgb(7, 203, 0);
  }

  .total {
    border-top: 1.5px solid #e4e1e1;
    line-height: 35px;
    color: #06b106;
    font-family: "楷体";
    font-size: 20px;
    text-align: center;
    font-weight: 900;
    font-family: "楷体";
  }
</style>
