<template>
  <div class="detail">
    <div ref="list" class="list">
      <el-empty
        :image-size="250"
        description="空空的，什么都没有下注！"
        v-if="detailData.length === 0"
      ></el-empty>
      <el-row v-for="(d, index) in detailData" :key="d.time">
        <el-col :span="2">{{
          index + 1 > 9 ? index + 1 : "0" + (index + 1)
        }}</el-col>
        <el-col :span="8">{{ d.time }}</el-col>
        <el-col class="a" :span="6">{{ d.haoMa }}</el-col>

        <el-col class="b" :span="5"
          ><span class="c">{{ d.money }}</span> <span> | </span>
          {{ d.item.length * d.money }}￥</el-col
        >
        <el-col :span="3"
          ><el-button
            class="delBtn"
            size="mini"
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deleteHao(index)"
          ></el-button
        ></el-col>
      </el-row>
    </div>
    <div></div>
    <div></div>
  </div>
</template>

<script>
export default {
  name: "Detail",
  data() {
    return {
      detailData: [],
    };
  },

  created() {
    let { ymd } = this.getTime(new Date());
    this.detailData = JSON.parse(localStorage.getItem(ymd)).details;
  },
  activated() {
    console.log(this.detailData);
    this.$nextTick(() => {
      this.$refs.list.scrollTo({
        top: this.$refs.list.scrollHeight,
        behavior: "smooth",
      });
    });
  },
  mounted() {
    this.$bus.$on("updateDetails", (details) => {
      this.detailData = details;
    });
  },
    beforeDestroy() {
      this.$bus.$off('updateDetails')
    },
  watch: {
    detailData: {
      // 初始化时让handler调用一下
      immediate: true,
      handler() {
        //下注后，详情页滚动到底部
        this.$nextTick(() => {
          this.$refs.list.scrollTo({
            top: this.$refs.list.scrollHeight,
            behavior: "smooth",
          });
        });
      },
    },
  },

  methods: {
    // 退码
    deleteHao(index) {
      this.$confirm(
        `确定删除 ${this.detailData[index].haoMa} = ${this.detailData[index].money} 吗?`,
        "",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(() => {
          const data = this.detailData[index];
          data.money = -data.money;

          console.log(data);

          this.$bus.$emit("update", data, index);

          console.log(index, this.detailData);

          // this.$message({
          //   type: "success",
          //   message: "删除成功!",
          //   center: true,
          //   duration: 1000,
          //   offset: 70,
          // });
          this.$notify({
            message: `删除成功！`,
            type: "success",
            position: "top-right",
            offset: 50,
            duration: 3000,
          });

          //更新数据
          // let { ymd } = this.getTime(new Date());
          // this.detailData = JSON.parse(localStorage.getItem(ymd)).details;
        })
        .catch(() => {
          this.$notify({
            message: `已取消删除！`,
            type: "info",
            position: "top-right",
            offset: 50,
            duration: 3000,
          });
        });
    },
    // 获取时间,ymd:年月日,hms:时分秒
    /**
     * {
     *    ymd:'',
     *    hms:''
     */
    getTime(date) {
      let month = zeroFill(date.getMonth() + 1); //月
      let day = zeroFill(date.getDate()); //日
      let hour = zeroFill(date.getHours()); //时
      let minute = zeroFill(date.getMinutes()); //分
      let second = zeroFill(date.getSeconds()); //秒

      //年月日
      let hms = hour + ":" + minute + ":" + second;

      let ymd = date.getFullYear() + "-" + month + "-" + day;

      return { ymd: ymd, ymdhms: hms };

      /**
       * 补零
       */
      function zeroFill(time) {
        if (time >= 0 && time <= 9) {
          return "0" + time;
        } else {
          return time;
        }
      }
    },
  },
};
</script>

<style lang="css" scoped>
.detail {
  font-family: "Times New Roman";
  font-size: 18px;
}

.detail .list {
  /* background-color: #1c1a1a; */
  height: 485px;
  overflow: auto;
  padding: 10px 20px;
}

.detail .list .el-row {
  line-height: 30px;
  border-bottom: solid 1px rgb(216, 219, 217);
  padding: 5px 0px;
  text-align: center;
}

.detail .list .el-row .hao {
  text-align: left;
}

.a{
  font-family: '微软雅黑';
}
.a,
.b {
  /* font-family: ''; */
  text-align: left;
}

.b {
  color: rgb(5, 174, 5);
}

.b .c {
  border: 1px solid;
  padding: 3px;
  text-align: center;
  border-radius: 50%;
  font-size: 16px;
  font-weight: 600;
}
</style>
