<template>
  <div class="static">
    <div class="head">
      <el-row>
        <el-col :span="4" :class="myClass[0]"
          ><div @click="flag = 0">8 X X 8 <strong>(头尾)</strong></div></el-col
        >
        <el-col :span="4" :class="myClass[1]"
          ><div @click="flag = 1">8 X 8 X</div></el-col
        >
        <el-col :span="4" :class="myClass[2]"
          ><div @click="flag = 2">8 8 X X</div></el-col
        >
        <el-col :span="4" :class="myClass[3]"
          ><div @click="flag = 3">X 8 8 X <strong>(中肚)</strong></div></el-col
        >
        <el-col :span="4" :class="myClass[4]"
          ><div @click="flag = 4">X X 8 8</div></el-col
        >
        <el-col :span="4" :class="myClass[5]"
          ><div @click="flag = 5">X 8 X 8</div></el-col
        >
      </el-row>
    </div>

    <div class="show">
      <el-row v-if="flag === 0" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= 0 ? 0 : 1 }">
            <p>{{ i - 1 }}XX{{ index }}</p>
            <span>{{ value }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 1" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= 0 ? 0 : 1 }">
            <p>{{ i - 1 }}X{{ index }}X</p>
            <span>{{ value }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 2" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= 0 ? 0 : 1 }">
            <p>{{ i - 1 }}{{ index }}XX</p>
            <span>{{ value }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 3" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= 0 ? 0 : 1 }">
            <p>X{{ i - 1 }}{{ index }}X</p>
            <span>{{ value }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 4" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= 0 ? 0 : 1 }">
            <p>XX{{ i - 1 }}{{ index }}</p>
            <span>{{ value }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row v-if="flag === 5" v-for="i in 10" :key="i">
        <el-col v-for="(value, index) in datas[flag][i - 1]" :key="index">
          <div :style="{ opacity: value <= 0 ? 0 : 1 }">
            <p>X{{ i - 1 }}X{{ index }}</p>
            <span>{{ value }}</span>
          </div>
        </el-col>
      </el-row>

      <div class="total">
        此页数目：{{ total[flag] }} &nbsp;&nbsp;全部数目：{{
          total[0] + total[1] + total[2] + total[3] + total[4] + total[5]
        }}
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "HistoryStatic",
    components: {},
    data() {
      return {
        flag: 0,
      };
  },
    props:["datas"],
    computed: {
      myClass() {
        let tmp = ["", "", "", "", "", ""];
        tmp[this.flag] = "activated";
        return tmp;
      },

      //头尾总数
      total() {
        const arr = this.datas;
        const total = [0, 0, 0, 0, 0, 0];

        arr.forEach((arr2, index) => {
          arr2.forEach((arr3) => {
            arr3.forEach((value) => {
              total[index] += value;
            });
          });
        });
        return total;
      },
    },
  };
</script>

<style lang="css" scoped>
.static{
  margin-bottom: 10px;
  width: 100%;
}
  .static .head .el-row {
    line-height: 55px;
  }

  .activated {
    background-color: #bfc;
  }

  .show .el-row .el-col {
    border: 1.5px solid #e4e1e1;
    padding: 5px;
    width: 10%;
  }

  .show .el-row .el-col {
    border-right: 0;
    border-bottom: 0;
  }
  .show .el-row:hover,
  .total:hover {
    background-color: #bfc;
  }

  .show .el-row .el-col:first-child {
    border-left: 0;
  }

  .show .el-row .el-col span {
    color: rgb(7, 203, 0);
  }

  .show .total {
    border-top: 1.5px solid #e4e1e1;
    line-height: 35px;
    color: #06b106;
    font-size: 20px;
    text-align: center;
    font-weight: 900;
    font-family: "楷体";
  }
</style>
